.comments {
    &__container {
        max-width: 800px;
        margin: 0 auto;
        padding: 20px;
        border: 1px solid #ddd;
        background-color: #f9f9f9;
    }

    &__title {
        font-size: 24px;
        margin-bottom: 20px;
        text-align: center;
        color: #333;
    }

    &__form {
        margin-bottom: 30px;
    }

    &__form-element {
        display: flex;
        flex-direction: column;
    }

    &__form-group {
        margin-bottom: 15px;
    }

    &__label {
        margin-bottom: 5px;
        font-weight: bold;
        color: #555;
    }

    &__input, 
    &__textarea {
        width: 100%;
        padding: 10px;
        border: 1px solid #ccc;
        border-radius: 4px;
        font-size: 16px;
    }

    &__button {
        padding: 10px 20px;
        background-color: #007bff;
        border: none;
        border-radius: 4px;
        color: white;
        font-size: 16px;
        cursor: pointer;

        &:hover {
            background-color: #0056b3;
        }
    }

    &__list {
        margin-top: 20px;
    }

    &__item {
        padding: 15px;
        border-bottom: 1px solid #eee;
    }

    &__author {
        font-size: 18px;
        margin-bottom: 5px;
        color: #333;
    }

    &__text {
        font-size: 16px;
        color: #555;
    }
}
